.and-header{
    position: fixed;
    z-index: 1000;

    
    &_content{
        height: 60px;
        max-height: 60px;
        // background-color: #1C242B;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 10px;
        padding: 20px;
        // box-shadow: 0 -1em 1.2em rgba(208, 208, 208, 0.6);
        overflow: hidden;
        user-select: none;
        

        width: 100vw;

        // background-color: blue;
        
        &_nav{
            ul {
                list-style: none;
                display: flex;
                white-space: nowrap;
                font-size: 12px;
                height: auto;
              
                
            
                li {
                    font-size: clamp(.5rem, 2vw, 1.2rem);
                    font-weight: 300;
                    transition: transform linear .5s;
                    
                
                    a {
                        text-decoration: none;

                        span{
                            transition: color ease .5s;
                            color: rgb(0, 0, 0);
                            
                            padding: 3px 25px;
                            margin: 0px 10px ;
                            // font-size: 18px;
                            border-radius: 50px;

                            font-family: gt, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;

                            &.active{
                                background-color: rgba(255, 255, 255, 0.543);
                            }
                        }

                        

                    }
                    
                }

                // li:hover{
                //     transform: translateY(-5px);
                // }
            
  
            }

            .icon-menu{
                display: none;
                fill: rgba(230, 210, 210, 0.648);
                transform: scale(1.5);
                margin-right: 25px;
            }

            
        }
        

        
    }

    

}



@media only screen and (max-width: 450px) {

    .and-header{
        
        &_content{
            height: 50px;
            justify-content: end;
            padding-right: 20px;

            &_nav{
                ul {
                    display: none;
                }

                .icon-menu{
                    display: block;
                }
                
                
            }
        }
    }
}

