
.wrap{
    
    background-color: #11111D;
    &_body{
        transition: transform 0.20s ease-out;
        max-height: 100vh;
        overflow: auto;

        &.open-menu {
            transform: scale(0.7) translateX(-90%);
            border-radius: 15px;
            overflow: hidden;
        }
        
        &_dashboard {
            position: relative;
            background-image: linear-gradient(to top, rgba(0, 0, 0, 0.85), rgba(0, 0, 0, 0.359)), url('../../assets/img/dashboard-1.webp');
            height: 100vh;
            background-size: cover;
            background-position: center;
            z-index: 100;        
            
            &_img{
                position: absolute;
                width: 100%;
                height: 100vh;
                overflow: hidden;
                object-fit: cover;

                > img {
                        
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
                
            }
        
            &_content_photo {
                position: relative;
                height: auto;
                display: flex;
                justify-content: center;
                align-items: center;
                z-index: 1;

        
                &_box {
                    max-width: 150px;
                    min-width: 30vh;
                    min-height: 30vh;
                    max-height: 150px;
                    border-radius: 50%;
                    overflow: hidden;
                    margin: 80px 0px 20px 0px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    // border: 6px solid rgba(208, 208, 208, 0.164);
                    user-select: none;
                    background-color: black;
                    margin-top: 20vh;
        
                    > img {
                        
                        width: 100%;
                        object-fit: cover;
                    }
                }
            }
        
            &_content_text{
                width: 100%;
                height: auto;
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;
                font-size: 12px;
        
                >label{
                    font-size: clamp(2rem, 4vw, 3rem);
                    font-weight: bold;
                    color: #F4594C;
                    transform: translateY(40px);
                    opacity: 0;
                    animation: textAnimation 2s ease forwards; 
                }
        
                >span{
                    font-size: clamp(1rem, 2vw, 1.5rem);
                    color: #becbd9;
                    opacity: 0;
                    animation: textAnimation 3s ease forwards; 
                    animation-delay: 2s;
                }
        
                @keyframes textAnimation {
                    to {
                        transform: translateY(0px);
                        opacity: 1;
                    }
                }
            }
        
            @keyframes borderAnimation {
                to {
                    transform: scale(1);
                    opacity: 1;
                }
            }
        }

        &_about{
            background-color: #101418;
            height: auto;
            // height: 100vh;
            z-index: 100;
            display: flex;
            justify-content: center;
            padding: 10px;

            &_content{
                // background-color: #ffffff36;
                // width: 100%;
                width: clamp(700px,75%,13200px);
                max-width: 1152px;
                height: auto;
                height: 100vh;
                margin-top: 50px;

                display: grid;
                gap: 5px;
                grid-template-columns: repeat(auto-fit,minmax(min(100%,400px), 1fr));

                &_title{
                    // background-color: red;
                    display: flex;
                    flex-direction: column;
                    gap: 5px;
                    padding: 20px;

                    &_header{
                        // background-color: blue;
                        display: flex;
                        align-items: center;
                        margin-bottom: 15px;

                        &_line{
                            background-color: #F4594C;
                            height: 2px;
                            width: 30px;
                        }

                        &_text{
                            // background-color: gray;
                            font-size: 28px;
                            // padding: 12px;
                            margin-left: 10px;
                            font-weight: 100;
                            letter-spacing: 2px;
                        }
                    }


                    // span{
                    //     font-size: 42px;
                    //     font-weight: bold;
                    // }

                    label{
                        font-size: 24px;
                    }
                }

                &_text{
                    font-size: 18px;
                    padding: 20px;
                }
            }
        }
    }

    &_body::-webkit-scrollbar {
        width: 7px;
    }
    &_body::-webkit-scrollbar-thumb {
        background-color: rgb(71, 71, 71); 
        border-radius: 10px;
    }
    &_body::-webkit-scrollbar-track {
        background-color: black;
    }
}


