.and-menu {
    position: absolute;
    top: 0;
    left: 30%;
    height: 100vh;
    width: calc(100vw - 30%);
    font-size: 12px;
    display: flex;
    flex-direction: column;
    overflow: hidden;

    &_header{
        height: auto;

        display: flex;
        align-items: center;
        justify-content: flex-end;
        background-color: #161720;  
        box-shadow: 0px 2px 9px 5px #13141d;  
        z-index: 100;





        &_icon{
            padding: 5px 0;
            // fill: rgba(230, 210, 210, 0.648);
            // transform: scale(1.3);
            margin-right: 10px;
      
            >span{
                font-size: 45px;
                color:rgba(230, 210, 210, 0.648);
            }

        }

        
        
    }

    &_items {
        background-color: #1B1C26;
        flex: 1;
        padding: 20px 0px;
    
        &_item {
            width: 100%;
            margin-top: 10px;
            color: rgba(230, 210, 210, 0.648);
            display: flex;
            align-items: center;
            padding: 5px 0;
            padding-left: 20px;
            user-select: none;
            font-size: 1.7em;
        
            &.active {
                background-color: #11111D;
                color: rgb(255, 255, 255);
                padding: 10px 0px 10px 15%; 
                font-size: 1.8em;
            }
        }
    }
  
    &_social {
        background-color: #1B1C26;
        height: auto;
        font-size: 2em;
        display: flex;
        align-items: center;
        justify-content: center;

        &_box{
            background-color: rgba(255, 255, 255, 0.121);
            margin: 20px 0px 20px 0px;
            height: auto;
            width: auto;
            overflow-x: scroll;
            border-radius: 100px;
            display: grid;
            grid-template-columns: 1fr 1fr 1fr 1fr;
            gap: 8px;
            padding: 8px;

            >img{
                width: 45px;
            }
        }

        &_box::-webkit-scrollbar {
            width: 0em; 
            height: 0px;
        }
    }
}